.header {
  /* padding: 0px 15px; */
  height: 100vh;
  background-color: #e9d5a1;
}
.header__container {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.header__content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.header__name {
  margin-bottom: 15px;
}
.header__link > a {
  /* transition: 0.5s;
  animation: fade-in;
  animation-delay: 1s; */
}

.header__link svg:hover .octo-arm {
  animation: octocat-wave 560ms ease-in-out;
}
.header__description {
  width: 100%;
  text-align: center;
}
.header__description span {
  font-size: 36px;
}

@keyframes octocat-wave {
  0%,
  100% {
    transform: rotate(0);
  }
  20%,
  60% {
    transform: rotate(-25deg);
  }
  40%,
  80% {
    transform: rotate(10deg);
  }
}
