.skills {
  padding: 80px 15px;
  background-color: #1f1f1f;
  color: #fff;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}
.skills__title {
  font-size: 36px;
  margin-bottom: 40px;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
}
.skills__list {
  display: flex;
  column-gap: 25px;
  justify-content: center;
}
.skills__item {
  padding: 15px 25px;
  border-radius: 8px;
  background-color: rgba(248, 245, 244, 0.05);
  width: 125px;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  align-items: center;
  justify-content: center;
}
.skills__capture {
  font-weight: 500;
}
